    let show_modal = false;
    let modal_message =
        "Thanks for contacing Wesics. Your Message has been sent successfully! We'll get back on your query soon";
    let modal_title = "Success";

    let mobile;
    let email;
    let name;
    let subject;
    let message;
    const validateEmail = (email) => {
        return String(email)
            .toLowerCase()
            .match(
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            );
    };

    async function send_message() {
        console.log("Sending request");
        // Calls the external refresh token create endpoint
        var params = new URLSearchParams();

        if (email == null || !validateEmail(email)) {
            modal_title = "Error";
            modal_message = "Invalid email. Kindly check email and try again";
            show_modal = true;
        } else if (subject == null || subject.length < 2) {
            modal_title = "Error";
            modal_message =
                "Subject empty. Kindly enter subject (Minimum 3 characters) and try again";
            show_modal = true;
        } else if (message == null || message.length < 5) {
            modal_title = "Error";
            modal_message =
                "Message empty. Kindly enter Message (Minimum 5 characters) and try again";
            show_modal = true;
        } else if (name == null || name.length < 3) {
            modal_title = "Error";
            modal_message =
                "Name empty. Kindly enter name (Minimum 3 characters) and try again";
            show_modal = true;
        } else {
            params.append("name", name);
            params.append("email", email);
            params.append("mobile", mobile);
            params.append("subject", subject);
            params.append("message", message);

            console.log(params);
            const response = await fetch_json(
                "https://api.in.zovibe.com/api/wesics/query",
                params
            );
            if (response.status == "success") {
                console.log(response);
                modal_title = "Success";
                let modal_message =
                    "Thanks for contacing Wesics. Your Message has been sent successfully! We'll get back on your query soon";
                show_modal = true;
            } else {
                modal_title = "Error";
                modal_message = "Some Error! Please try again";
                if (response.message != null && response.message.length > 4) {
                    modal_message = response.message;
                }
                show_modal = true;
            }
        }
    }
    function handleChange(e) {
        console.log(e.detail);
    }
    function onwheel(e){
    
        
        

        let pos = go_big_ele.getBoundingClientRect();

        if(pos != null){
            if(window_height == null){
        window_height = (window.innerHeight);
        window_width = (window.innerWidth);

            }
        let full_text_size = Math.ceil(window_width/6.5);
            let current_top_perc = ((pos.y+(pos.height/1.8))/window_height)*100;
            if(current_top_perc > 30 && current_top_perc < 101){
                let new_text_size = Math.ceil((full_text_size/2.5)+(full_text_size * ((100-current_top_perc)/100)));
                if(new_text_size > full_text_size){new_text_size = full_text_size;}
                big_text_size = new_text_size + "px";

            }else if(current_top_perc < 30){
                big_text_size = full_text_size + "px";
            }
        }
        //console.log(e,"go_big_ele",pos.y,pos.top,pos.height,window.innerHeight)
    }

let window_height;
