
import fetch from "node-fetch";

export async function fetch_json(url,params){

    var result = { status: "failed" };
    console.log(params);
    try {
        const response = await fetch(
            url,
            {
                method: "POST",
                body: params,
                credentials: "same-origin",
            }
        );
        let data = await response;
        let data_json = await data.json();
        result = data_json;
    } catch (e) {
        console.error(e);
    }

    return result;
}